.header {

  position: absolute;
  padding-top: 20px;
  width: 100%;
  z-index: 100;
  top: 0;
  left: 0;

  &__container{
    display: flex;
    justify-content: space-between;
  }

  &__text{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
    font-size: 20px;
    color: var(--gold-color);

    span{
      display: block;
    }
  }
  &__right{
    display: flex;
    gap: 300px;
  }
  &__logo{
    width: 60px;
    height: 60px;
    margin: 0;

  }
}

.menu {
&__icon {
    display: block;
    width: 50px;
    height: 20px;
    position: relative;
    z-index: 5;
    cursor: pointer;
    span {
      position: absolute;
      top: calc(50% - 1px);
      left: 0;
      width: 100%;
      height: 3px;
      background-color: var(  --brown-color);
      transition: all 0.2s;
      &:first-child {
        top: 0;
      }
      &:last-child {
        top: auto;
        bottom: 0;
      }
    }
    &.active {
      span {
        transform: scale(0);
        &:first-child {
          transform: rotate(-45deg);
          top: calc(50% - 1px);
        }
        &:last-child {
          transform: rotate(45deg);
          bottom: calc(50% - 1px);
        }
      }
    }

}
&__body {
    position: fixed;
    top: -15px;
    left:-100%;
    height: 100vh;
    width: 100%;
    transition: all 0.2s;
    z-index: 2;

  &.active {
    left: 0;
  }
}

&__list {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    padding: 60px 10px 10px 40px;
    gap: 20px;
    height: 100%;
    width: 100%;
    background-color: var(--bg-color);
    overflow: auto;
}
    &__link {
    font-family: Montserrat;
    font-size: 24px;
    font-weight: 700;
    line-height: 29px;
    letter-spacing: 0em;
    color: var(--gold-color);

    @include hover{
      color: #000;
    }


  @media (max-width: 768px) {
    display: block;
    font-size: 20px;
    line-height: 30px;
  }
}
}
@include tablet{
  .header{
    padding-top: 15px;
  }
  .header__text{
    font-size: 16px;
  }
  .header__logo{
    width: 50px;
    height: 50px;
  }
}
@include mobile{
  .header__text{
    font-size: 12px;
  }
  .header__logo{
    font-size: 20px;
  }
}
