.feedback{
  background-color: var(--bg-color);
  padding: 60px 0px 200px 0px;

  &__title{
    max-width: 350px;
    padding-left: 100px;
  }
  &__inner{
    padding: 20px;
    border-radius: 20px;
    background-color: #fff;
    border: 5px solid var(--gold-color);
    -webkit-box-shadow: 25px 20px 13px -6px rgba(178, 144, 87, 0.3);
    -moz-box-shadow: 25px 20px 13px -6px rgba(178, 144, 87, 0.3);
    box-shadow: 25px 20px 13px -6px rgba(178, 144, 87, 0.3);
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }
  &__img{

    object-fit: contain;
    border-radius: 20px;
    padding: 30px;
    border-radius: 20px;
    width: 480px;
    height: 330px;

    // &-wide{
    // height: 290px;
    // }
    // &-wide1{
    //   height: 220px;
    //   }
  }
  &__img-big{
    width: 480px;
    height: 550px;
  }
  &__insta{
    margin-top: 50px;
    display: flex;
    gap: 30px;
    justify-content: center;
    align-items: center;
  }
  &__text{
    margin: 0;
    font-size: 36px;
    line-height: 50px;
    font-weight: 350;
    color: #000;
  }
  &__btn{
    font-size: 26px;
    padding: 10px;
  }
  &__icon{
    display: block;
    width: 60px;
    height: 60px;
    fill: var(--gold-color);
  }
}
@include big-desktop {
  .feedback__inner{
    margin: 0 auto;
    max-width: 960px;
  }
  .feedback__inner{
   grid-template-columns: repeat(2, 1fr);
  }
}
@include small-desktop{
  .feedback__title{
    padding-left: 10px;
  }
}
@include tablet{
  .feedback__container{
    padding: 0px 0px 200px 0px;
  }
  .feedback__title{
    max-width: 200px;
  }
  .feedback__inner{
    max-width: 460px;
    border: none;
    box-shadow: none;
    grid-template-columns: repeat(1, 1fr);
   }
   .feedback__img{
    &:nth-child(1){
      margin: -50px 0px 0px 0px;
    }
    &:nth-child(2){
      margin-top: -120px;
    }
    &:nth-child(3){
      margin-top: -100px;
    }
    &:nth-child(4){
      margin: -100px 0px 0px 0px;
     }
     &:nth-child(5){
      margin: -100px 0px 0px 0px;
     }
     &:nth-child(6){
      margin: -100px 0px 0px 0px;
     }
   }
}
@include small-tablet{
  .feedback__text{
    font-size: 26px;
    line-height: 30px;
  }

}
@include mobile{
  .feedback__inner{
   max-width: 350px;
  }
  .feedback__img{
    width: 330px;
    height: 250px;
  }
}
@include mobile-small{
  .feedback__img{
    width: 280px;
    height: 180px;
  }
  .feedback__text{
    font-size: 22px;
    line-height: 30px;
  }
}
