.contacts{
   background-color: var(--bg-color);

   &__container{
    padding: 30px 100px;
   }

   &__title{
    margin: 0;
    padding-bottom: 20px;
    font-size: 50px;
    line-height: 60px;
    color: var(--gold-color);
   }
   &__inner{
    display: flex;
    gap: 30px;
   }

   &__adress{
    margin: 0;
    max-width: 600px;
    font-size: 30px;
    line-height: 40px;
    font-weight: 700;
    color: var(--brown-color);

    span{
      font-weight: 500;
      color: var(--gold-color);
    }
   }
   &__text{
    margin: 0;
    font-size: 25px;
    line-height: 25px;
    font-weight: 350;
    color: var(--brown-color);
   }
   &__tel{
    font-weight: 500;
    font-size: 36px;
    line-height: 36px;
    color: var(--gold-color);
   }
   &__social{
    margin-top: 10px;
    display: flex;
    gap: 5px;
    margin-bottom: 10px;
   }
   &__icon{
    width: 40px;
    height: 40px;
    fill: var(--gold-color);
   }
   &__map{
    margin: 0 auto;
    border-radius: 10px;
    filter: brightness(0.6);
    max-width: 600px !important;
   }

   &__form{
    background-image: url('../img/offer-bg.jpeg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: right;
    margin-top: 10px;
    padding: 20px;
    padding-top: 26px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    width: 600px;
    border-radius: 10px;
  }
  &__input{
    outline: none;
    opacity: 0.6;
    padding: 0px 15px;
    background-color: var(--light-color);
    width: 550px;
    height: 50px;
    border: 1px solid #41383D;
    border-radius: 10px;

    &::placeholder{
      font-family: Montserrat;
      font-size: 20px;
      font-weight: 550;
      line-height: 24px;
      letter-spacing: 0.02em;
      text-align: left;
      color: #B4B4B4;
    }
  }

  &__btn{
    border-radius: 10px;
    opacity: 0.6;
    border: 1px solid #41383D;
    width: 550px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--brown-color);
    font-family: Montserrat;
    font-size: 30px;
    font-weight: 700;
    line-height: 44px;
    letter-spacing: 0em;
    text-align: left;
    color: var(--gold-color);
    cursor: pointer;
  }
  &__sub{
    margin: 0;
    text-align: center;
    color: var(--gold-color);
    font-size: 20px;
    line-height: 20px;
    font-weight: 500;
  }
}
@include big-desktop {
  .contacts__container{
    padding: 30px 20px;
  }
  .contacts__adress{
    font-size: 30px;
  }
  .contacts__text{
    display: block;
  }
}
@include small-desktop{
  .contacts__inner{
    flex-direction: column;
    align-items: center;
  }
}
@include small-tablet{

  .contacts__title{
    padding: 0px 0px 20px 0px;
    font-size: 40px;
    line-height: 40px;
  }
  .contacts__adress{
    max-width: 500px;
    font-size: 25px;
    line-height: 35px;
  }
  .contacts__tel{
    font-size: 26px;
    line-height: 26px;
  }
  .contacts__text{
    font-size: 20px;
    line-height: 20px;
  }
  .contacts__map{
    border-radius: 15px;
    max-width: 500px !important;
    height: 250px !important;
  }
  .contacts__form{
    max-width: 500px;
  }
  .contacts__input{
    max-width: 450px;
  }
  .contacts__btn{
    width: 450px;
  }
}
@include mobile{
  .contacts__container{
    padding: 10px;
  }
  .contacts__form{
    margin: 0 auto;
    width: 380px;
  }
  .contacts__input{
    width: 350px;
  }
  .contacts__map{
    border-radius: 15px;
    max-width: 380px !important;
  }
  .contacts__btn{
    width: 350px;
  }
}
@media (max-width: (400px)){
  .contacts__form{
    width: 300px;
  }
  .contacts__sub{
    font-size: 16px;
  }
  .contacts__input{
    width: 280px;
  }
  .contacts__map{
    border-radius: 15px;
    max-width: 300px !important;
  }
  .contacts__btn{
    width: 280px;
  }
}
