.footer{
  background-color: var(--bg-color);
  &__container{

    background-image: url('../img/wave.svg');
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: 130px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding-bottom: 20px;
  }

  &__name{
    margin: 0;
    font-size: 30px;
    font-weight: 500;
    line-height: 40px;
    color: var(--gold-color);
  }
  &__logo{
    margin: 0;
    font-size: 30px;
    font-weight: 500;
    line-height: 40px;
    color: var(--gold-color);
  }
  &__contacts{
    margin: 0;
    font-size: 12px;
    font-weight: 350px;
    color: var(--gold-color);
  }
  .nav{

    &__list{
      display: flex;
      gap: 20px;
    }
    &__link{
      font-size: 20px;
      font-weight: 500;
      color: var(--gold-color);

    }

  }
}
@include small-tablet {
  .nav__list{
    flex-direction: column;
    align-items: center;
    gap: 5px !important;
  }
  .nav__link{
    font-size: 14px !important;
  }
}
