.sertificate{

  background-color: var(--bg-color);
  padding-bottom: 30px;

  &__container{
    background: rgb(47,30,24);
    background: linear-gradient(180deg, rgba(47,30,24,1) 0%, rgba(47,30,24,1) 70%, rgba(255,246,237,1) 100%);


  }
  &__inner{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 20px;
  }
  &__title{
    margin: 0;
    text-align: center;
    font-size: 50px;
    font-weight: 350;
    color: var(--gold-color);
  }
  &__subtitle{
    margin: 0;
    text-align: center;
    font-size: 40px;
    line-height: 50px;
    font-weight: 350;
    color: var(--light-color);
    margin-bottom: 30px;
  }
  &__img{
    border: 1px solid var(--gold-color);
    border-radius: 20px;
    width: 400px;
    height: 270px;

    &-big{
      width: 320px;
      height: 450px;
    }
  }
}
@include tablet {
  .sertificate__title{
    font-size: 40px;
  }
  .sertificate__subtitle {
    font-size: 40px;
  }
}
@include small-tablet{
  .sertificate__title{
    font-size: 30px;
  }
  .sertificate__subtitle {
    font-size: 30px;
  }
}
@include mobile {
  .sertificate__title{
    font-size: 26px;
  }
  .sertificate__subtitle {
    font-size: 26px;
    line-height: 30px;
  }
}
