.price{
  background-color: var(--bg-color);

  &__title{
    margin: 0;
    max-width: 320px;
    padding: 0px 0px 50px 90px;
    font-size: 50px;
    color: var(--gold-color);
    position: relative;

    &::after{
      content: '';
      position: absolute;
      right: 0px;
      top: 30px;
      border-radius: 10px;
      width: 70px;
      height: 3px;
      background-color: var(--gold-color);
      color: var(--gold-color);
    }
  }
}
.accordion{
  margin: 0 auto;
  max-width: 1000px;


  &__control {
    text-align: left;
    width: 100%;
    background-color: transparent;
    cursor: pointer;
    font-size: 20px;
    line-height: 20px;
    color: #242424;
    border-bottom: 1px solid var(--gold-color);
    }

  &__content {
    font-weight: 400;
    font-size: 20px;
    color: #000;
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    padding: 0px 10px;
    will-change: max-height;
    transition: all 0.3s ease-out;
    box-sizing: content-box;
    padding-bottom: 10px;
    border-bottom: 1px solid var(--gold-color);
  }
  &__title {
    margin: 0;
    font-weight: 400;
    font-size: 26px;
    font-weight: 500;
    letter-spacing: 0em;
    text-align: left;
    color: #2F4550;

  }

  &__descr{
    margin: 0;
    font-size: 20px;
    font-weight: 350;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
    color: #2F4550;
  }
  &__link{
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    font-size: 16px;
    width: 150px;
    height: 40px;
    background-color: var(--gold-color);
    color: var(--light-color);
  }
  &__icon-box{
    margin-top: 5px;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color:var(--brown-color);
    min-width: 30px;
    height: 30px;
    margin-left: 10px;
  }

  &__icon {
    transform: rotate(45deg);
    border-radius: 100%;
    display: inline-block;
    fill: #fff;
    width: 25px;
    height: 25px;
    background-image: url("../img/svg/cloze.svg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    transition: transform 0.3s ease-out;
    padding-left: 15px;
  }

  &__box{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  }
  .open .accordion__control{
    border-bottom: none;
  }

  .open .accordion__icon {
    transform: rotate(90deg);
  }

  .open .accordion__content {
    opacity: 1;
    padding-bottom: 18px;}

@include small-desktop{
  // .price__container{
  //   padding: 20px;
  // }
  .price__title{
    padding: 50px 0px 50px 0px;
    max-width: 280px;
    &::after{
      content: '';
      position: absolute;
      right: 0px;
      top: 80px;
    }
  }
  }
  @include tablet{
    // .price__container{
    //   padding: 100px 0px 30px 0px;
    // }
    .price__title{
      max-width: 200px;
      font-size: 30px;
      line-height: 40px;
      &::after{
        content: '';
        position: absolute;
        right: 0px;
        top: 70px;
      }
    }
    .accordion__title{
      font-size: 18px;
    }
    .accordion__descr{
      font-size: 16px;
      line-height: 25px;
    }
  }

