.services{
  background-color: var(--bg-color);


  &__container{
    padding: 100px 100px 100px 200px;
    background: linear-gradient(180deg, rgba(255,246,237,1)  0%, rgba(47,30,24,1) 80%,  rgba(47,30,24,1)100%);
    position: relative;
  }
  &__title{
    margin: 0;
    padding: 20px 10px 10px 30px;
    font-size: 40px;
    font-weight: 350;
    line-height: 50px;
    color: var(--gold-color);
  }
  &__left{
    border-radius: 20px;
    background-image: url('../img/photo-3.jpg');
    object-fit: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 600px;
    height: 700px;
    background-repeat: no-repeat;
    background-size: cover;
  }
  &__img{
    position: absolute;
    display: block;
    bottom: 100px;
    left: 820px;
    border-radius: 10px;
    width: 350px;
    height: 400px;
  }
  &__list{
    color: var(--light-color);
  }
  &__item{
    max-width: 500px;
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }
  &__text{
    margin: 0;
    font-weight: 350;
    font-size: 26px;
    line-height: 40px;
    color: var(--light-color);
  }
}
@include big-desktop{
  .services__container{
    padding: 100px;
  }
  .services__img{
    left: 750px;
  }
}
@include small-desktop {
  .services__container{
    position: static;
    padding: 100px 20px 100px 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .services__text{
    font-size: 22px;
    line-height: 30px;
  }
  .services__left{
    width: 550px;
    height: 600px;
  }
  .services__img{
    position: static;
    margin: 20px  0px 0px 140px !important;
  }
}
@include small-tablet{
  .services__left{
    width: 500px;
    height: 550px;
  }
  .services__img{
    width: 330px;
    height: 370px;
    margin: 20px  0px 0px 70px;
  }
  .services__text{
    font-size: 20px;
    line-height: 26px;
  }
}
@include mobile {
 .services__left{
    width: 300px;
    height: 350px;
  }
  .services__img{
    width: 215px;
    height: 235px;
    margin: 5px  0px 0px 60px !important;
  }
  .services__list{
    margin-left: -10px;
  }
  .services__title{
    padding: 10px;
    font-size: 20px;
    line-height: 25px;
  }
  .services__item{
    &:not(:last-child){
      margin-bottom: 5px;
    }
  }
  .services__text{
    font-size: 12px;
    line-height: 16px;
    line-height: 20px;
  }
}
@include mobile-small {
  .services__left{
    width: 280px;
    height: 350px;
  }
  .services__img{
    width: 215px;
    height: 250px;
    margin: 0px  0px 0px 20px;
  }
}

