/* stylelint-disable */

@mixin for-desktop {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}
@mixin small-desktop {
  @media (max-width: (1300px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (992px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
@mixin small {
  @media (max-width: (400px)) {
    @content;
  }
}

@mixin mobile-small {
  @media (max-width: (361px)) {
    @content;
  }
}

