.hero{
  background-color: var(--bg-color);


&__container{
  position: relative;
  padding: 100px 100px 0px 100px;
  min-height: 850px;
}

 &__left{
  position: relative;
 }
 &__title{
  margin: 0;
  font-size: 50px;
  font-weight: 350;
 }
 &__subtitle{
  margin: 0;
  font-size: 60px;
  line-height: 70px;
  font-weight: 500;
  color: var(--gold-color);
 }
 &__text{
  margin: 0;
  max-width: 680px;
  font-size: 20px;
  font-weight: 350;
  line-height: 30px;
  span{
    color: var(--gold-color);
  }
 }
 &__list{
  margin-left: 50px;
  color: var(--gold-color);
 }
 &__item{
  display: flex;
  align-items: center;
  gap: 10px;
 }
 &__icon{
  width: 10px;
  height: 15px;
  fill: var(--gold-color);
 }
 &__descr{
  margin: 0;
  font-family: Mont;
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: 0px;
  text-align: left;
  color: var(--gold-color);
 }
 &__img-box{
  position: absolute;
  bottom: 0;
  right: 0;
 }
 &__img{
  margin-top: -170px;
  width: 500px;
  height: 800px;
 }
 &__btn{
  margin: 0 auto;

 }
}

// Кнопка__________________________________________________________________
$framePad : 4px;
.frame-btn{
    line-height: $framePad * 2;
    display: inline-block;
    padding: ($framePad * 5) ($framePad * 6);
    font-size: $framePad * 3;
    letter-spacing: $framePad * 0.6;
    text-decoration: none;
    z-index: 2;
    position: relative;
    bottom: -40px;
    left: 450px;

    &-offer{
      left: 30px;
      bottom: -10px !important;
      font-size: 20px;
    }
	&__text{
    margin: 0;
    padding-left: 10px;
    color: #b2876f;
    font-weight: bold;
    text-transform: uppercase;
    transition: 300ms ease;
    position: relative;

		.frame-btn:hover & {
			color: white;
		}
	}
  .frame-btn__text-bottom{
    display: block;
    padding: 10px 10px 0px 50px;
  }
	&__solid{
		position: absolute;
		top: 0;
		left: 0;
		margin: $framePad * 2;
		background-color: var(--gold-color);
		width: calc(100% - (#{$framePad} * 4));
		height: calc(100% - (#{$framePad} * 4));
		transform-origin: 50%;
		transform: scale(0.85);
		opacity: 0;
		transition: 500ms cubic-bezier(0.165, 0.84, 0.44, 1);

		.frame-btn:hover & {
			opacity: 1;
			transform: scale(1);
		}
	}
	&__outline{
		position: absolute;
		top: 0;
		left: 0;

		&--tall{
			margin: 0 $framePad;
			width: calc(100% - (#{$framePad} * 2));
			height: 100%;
		}
		&--flat{
			margin: $framePad 0;
			height: calc(100% - (#{$framePad} * 2));
			width: 100%;
		}
	}
	&__line{
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;

		&--tall{
			&:before,
			&:after{
				height: 100%;
				width: $framePad * .2;
				top:0;

				.frame-btn:hover .frame-btn__outline--flat &{
					transform: scaleY(0);
				}
				.frame-btn:hover .frame-btn__outline--tall &{
					margin: $framePad 0;
					height: calc(100% - (#{$framePad} * 2));
				}
			}

			&:before{
				left: 0;
			}
			&:after{
				right: 0;
			}
		}
		&--flat{
			&:before,
			&:after{
				width: 100%;
				height: $framePad * .2;;
				left: 0;

				.frame-btn:hover .frame-btn__outline--tall &{
					transform: scaleX(0);
				}
				.frame-btn:hover .frame-btn__outline--flat &{
					margin: 0 $framePad;
					width: calc(100% - (#{$framePad} * 2));
				}
			}

			&:before{
				top: 0;
			}
			&:after{
				bottom: 0;
			}
		}
		&:before,
		&:after{
			content: '';
			display: block;
			position: absolute;
			background-color: #b2876f;
			transition: 800ms cubic-bezier(0.165, 0.84, 0.44, 1);
			.frame-btn:hover & {
				transition: 400ms cubic-bezier(0.165, 0.84, 0.44, 1);
			}
		}
	}
}
// Виждет позвонить______________________________________

.social {
  position: fixed;
  z-index: 100;
  bottom: 160px;
  right: -30px;
  transform: rotate(270deg);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: left;
  -ms-flex-pack: left;
  justify-content: left;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.social-icon{
  display: block;
  fill: var(--gold-color);
  width: 30px !important;
  height: 30px;

  &-what{
    width: 50px !important;
  }
}

.social-trigger {
  background: #111;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  margin-right: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  cursor: pointer;
  -webkit-transition: background-color .3s;
  -o-transition: background-color .3s;
  transition: background-color .3s;
}

.social-trigger svg {
  transform: rotate(90deg);
  width: 45px !important;
  height: auto;
  max-height: 100%;
  max-width: 100%;
}

.social-trigger svg path {
  fill: #c59500;
}

.social-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.social-link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  background-color: var(--brown-color);
  border-radius: 50%;
  margin-right: 10px;
  -webkit-transition: opacity .3s, visibility .3s, -webkit-transform .3s;
  transition: opacity .3s, visibility .3s, -webkit-transform .3s;
  -o-transition: opacity .3s, visibility .3s, transform .3s;
  transition: opacity .3s, visibility .3s, transform .3s;
  transition: opacity .3s, visibility .3s, transform .3s, -webkit-transform .3s;
  -webkit-transform: scale(0);
  -ms-transform: scale(0);
  transform: scale(0);
  -webkit-transform-origin: center;
  -ms-transform-origin: center;
  transform-origin: center;
  opacity: 0;
  visibility: hidden;
}

.social-link svg {
  transform: rotate(90deg);
  width: auto;
  height: auto;
  max-height: 100%;
  max-width: 100%;
}

.social-link svg path {
  fill: #c59500;
}

.social:not(.social_opened) .social-link {
-webkit-transition-delay: 0s !important;
-o-transition-delay: 0s !important;
transition-delay: 0s !important;
}

.social.social_opened .social-link {
opacity: 1;
visibility: visible;
-webkit-transform: scale(1);
-ms-transform: scale(1);
transform: scale(1);
}

