.examples{
  background-color: var(--bg-color);

  &__container {
    padding-top: 200px;
    min-height: 700px;
    background-image: url('../img/slider-bg.svg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  &__title{
    max-width: 500px;
  }
  &__inner{
    margin: 20px;
    border-radius: 20px;
    border: 5px solid var(--gold-color);
    width: 400px;
    height: 400px;
  }
  &__img{
    object-fit: contain;
    border-radius: 20px;
    width: 400px;
    height: 400px;
  }

  .swiper{
    border-radius: 20px;
    border: 5px solid var(--gold-color);
    margin: 0 auto;
    max-width: 1400px;
    height: 460px;
    border: 5px solid var(--gold-color);
    -webkit-box-shadow: 25px 20px 13px -6px rgba(178, 144, 87, 0.3);
    -moz-box-shadow: 25px 20px 13px -6px rgba(178, 144, 87, 0.3);
    box-shadow: 25px 20px 13px -6px rgba(178, 144, 87, 0.3);
  }

  .swiper-button-prev{
    border-radius: 30px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.05), 0px 20px 40px rgba(92, 103, 153, 0.2);
    background: var(--light-color);
    width: 44px;
    height: 44px;
    background-image: url('../img/prev.svg');
    background-repeat: no-repeat;
    background-position: center;

    &::after{
      display: none;
    }
  }

  .swiper-button-next{
    border-radius: 30px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.05), 0px 20px 40px rgba(92, 103, 153, 0.2);
    background: var(--light-color);
    width: 44px;
    height: 44px;
    background-image: url('../img/next.svg');
    background-repeat: no-repeat;
    background-position: center;

  &::after {
    display: none;
    }
  }
}
@include big-desktop{
  .examples__container{
    padding-top: 200px;
  }
  .swiper{
    width: 950px;
  }
}
@include tablet {
  .examples__container{
    padding-top: 170px;
  }
  .swiper{
    width: 460px;
  }
  .examples__title{
    max-width: 300px;
  }
}
@include mobile {
  .examples__container{
    padding-top: 100px;
    min-height: 500px;
  }
  .swiper{
    width: 350px !important;
    height: 350px !important;
  }
  .examples__inner{
    width: 290px;
    height: 290px;
  }
  .examples__img{
    width: 290px;
    height: 290px;
  }
}
@include mobile-small{
  .examples__container{
    min-height: 400px;
  }
  .swiper{
    width: 300px !important;
    height: 300px !important;
  }
  .examples__inner{
    width: 240px;
    height: 240px;
  }
  .examples__img{
    width: 240px;
    height: 240px;
  }
}
