.offer{
  background-color: var(--bg-color);
  &__container{
    position: relative;
    padding: 50px 100px 20px 100px;
    background: rgb(47,30,24);
    background: linear-gradient(180deg, rgba(255,246,237,1) 0%, rgba(47,30,24,1) 30%, rgba(255,246,237,1) 100%);

  }
  &__title{
    margin: 0;
    padding: 50px 0px 30px 0px;
    max-width: 700px;
    font-size: 30px;
    font-weight: 500;
    line-height: 40px;
    color: var(--gold-color);
  }
  &__inner{
    position: relative;
    display: flex;
    gap: 50px;
    margin-bottom: 50px;
  }
  &__img{
    border-radius: 100%;
    width: 350px;
    height: 350px;
    object-fit: cover;
  }
  &__test{
    margin: 0;
    font-size: 30px;
    font-weight: 500;
    line-height: 40px;
    color: var(--gold-color);
  }
  &__price{
    margin: 0;
    padding-left: 20px;
    font-size: 24px;
    font-weight: 350;
    line-height: 50px;
    color: var(--light-color);
  }
  &__price-decor{
    font-size: 30px;
    font-weight: 700;
    text-decoration: line-through;
  }
  &__price-gold{
    font-size: 30px;
    font-weight: 700;
    color: var(--gold-color);
  }
  &__box{
    background-size: cover;
    background-repeat: no-repeat;
    background-position: right;
    border-radius: 20px;
    border: 1px solid var(--light-color);
    margin: 0 auto;
    max-width: 900px;
    height: 200px;

  }
  &__box-inner{
    padding-top: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 20px;
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(4px);
    height: 100%;
  }
  &__price{
    margin: 0;
  }
  &__promotion{
    margin: 0;
    font-size: 35px;
    line-height: 60px;
    font-weight: 350;
    color: var(--light-color);

    span{
      font-size: 45px;
      font-weight: 700;
      color: var(--gold-color);
    }
  }
  &__bottom{
   margin: 100px;
   position: relative;
  }
  &__bottom-right{
    background-image: url('../img/photo-6.jpg');
    background-size: cover;
    position: relative;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center top;
    border-radius: 10px;
    width: 600px;
    height: 700px;
  }
  &__about{
    margin: 0;
    position: absolute;
    top: 20px;
    left: 30px;
    font-size: 50px;
    font-weight: 350;
    line-height: 60px;
    color: var(--gold-color);
  }
  &__img-right{
    width: 700px;
    height: 800px;
  }
  &__bottom-inner{
    border-radius: 10px;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.4);
    backdrop-filter: blur(2px);

  }
  &__insta{
    position: relative;
    margin-left: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    transform: rotate(-10deg);
    border:  3px solid var(--brown-color);
    width: 350px;
    height: 100px;


  }
  &__link{
    position: absolute;
    transform: rotate(10deg);
    top: 10px;
    left: 110px;
    font-size: 30px;
    line-height: 80px;
    font-weight: 350px;
    color: var(--brown-color);
  }
  &__bottom-left{
    background-image: url('../img/photo-4.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: absolute;
    bottom: -20px;
    left: 600px;
    border-radius: 10px;
    width: 450px;
    height: 500px;
  }
  &__descr{
    margin: 0;
    padding: 100px 70px 20px 30px;
    font-weight: 350;
    font-size: 22px;
    line-height: 30px;

    &-bottom{
      padding: 20px 100px 10px 30px;
    }
  }

}

@include big-desktop{
  .offer__container{
    padding: 50px 20px 100px 20px;
  }
  .offer__inner{
    justify-content: center;
    gap: 10px;
  }
  .offer__img{
    width: 280px;
    height: 280px;
  }
  .offer__title{
    padding: 20px 0px 20px 0px;
    max-width: 700px;
    font-size: 25px;
    line-height: 35px;
  }
  .offer__price{
    font-size: 20px;
    line-height: 30px;
  }
  .offer__price-decor{
    font-size: 25px;
  }
  .offer__price-gold{
    font-size: 25px;
  }
  .offer__box{
    max-width: 750px;
    height: 200px;
  }
}
@include small-desktop{
  .offer__bottom{
    margin: 20px;
    position: static;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .offer__bottom-right{
    margin: 0px;
    width: 550px;
    height: 600px;
  }
  .offer__bottom-left{
    position: static;
    margin: 20px  0px 0px 100px;
  }
  .offer__descr{
    padding: 90px 10px 10px 10px;
    font-weight: 350;
    font-size: 22px;
    line-height: 30px;
    &-bottom{
      padding: 10px 10px 10px 10px;
    }
  }
}

@include tablet{
  .offer__container{
    padding: 20px;
  }
  .offer__inner{
   display: block;
  }

  .offer__img{
    width: 250px;
    height: 250px;
  }
  .offer__right{
    position: absolute;
    top: 0px;
    right: 50px;
  }
  .offer__title{
    padding: 0;
    max-width: 500px;
    text-align: center;
    font-size: 16px;
    line-height: 25px;
  }
  .offer__price{
    text-align: center;
    font-size: 16px;
    line-height: 30px;
  }
  .offer__price-decor{
    font-size: 20px;
  }
  .offer__price-gold{
    font-size: 20px;
  }
  .offer__box-inner{
    padding-top: 0;
  }
  .offer__box{
    position: absolute;
    top: 170px;
    right: 100px;
    max-width: 450px;
    height: 100px;
  }
  .offer__promotion{
    padding: 10px;
    font-size: 18px;
    line-height: 20px;
    span{
      font-size: 25px;
    }
  }
}
@include small-tablet{
  .offer__bottom-right{
    margin: 0px;
    width: 500px;
    height: 550px;
  }
  .offer__bottom-left{
    width: 440px;
    height: 460px;
    margin: -10px  0px 0px 90px;
  }
  .offer__about{
    top: 10px;
    left: 10px;
    font-size: 30px;
    line-height: 40px;
  }
  .offer__descr{
    padding: 50px 10px 10px 10px;
    font-weight: 350;
    font-size: 20px;
    line-height: 30px;
    &-bottom{
      padding: 10px 10px 10px 10px;
    }
  }
  .offer__img{
    width: 200px;
    height: 200px;
  }
  .offer__right{
   position: static;
  }
  .offer__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 0;
  }
  .offer__container{
    position: static;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    gap: 30px;
  }
  .offer__box{
    position: static;
  }
  .offer__title{
    max-width: none;
  }
}

@include mobile{
  .offer__box{
    max-width: 300px;
    height: 80px;
  }
  .offer__price{
    text-align: center;
    font-size: 14px;
    line-height: 25px;
  }
  .offer__price-decor{
    font-size: 18px;
  }
  .offer__price-gold{
    font-size: 18px;
  }
  .offer__promotion{
    padding: 10px;
    font-size: 12px;
    line-height: 16px;
    span{
      font-size: 16px;
    }
  }
  .offer__bottom-right{
    margin: 0;
    width: 300px;
    height: 350px;
  }
  .offer__bottom-left{
    width: 235px;
    height: 270px;
    margin: 0px  0px 0px 50px;
  }
  .offer__about{
    font-size: 22px;
    line-height: 30px;
  }
  .offer__descr{
    padding: 40px 10px 10px 10px;
    font-weight: 350;
    font-size: 12px;
    line-height: 16px;
    &-bottom{
      padding: 5px;
    }
  }
}
@include mobile-small {
  .offer__bottom-right{
    margin: 0;
    width: 280px;
    height: 350px;
  }
  .offer__bottom-left{
    width: 215px;
    height: 250px;
    margin: 0px  0px 0px 20px;
  }
}

