// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "GTEestiProDisplay", sans-serif;
  --content-width: 1530px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --bg-color:#fff6ed;
  --brown-color: #2f1e18;
  --gold-color:#b29057;
}
